@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

:root {
  --color-bg-dark: #3a3a3a;
  --color-bg-black: #111111;
  --color-white: #fefefe;
  --color-primary: #7634d8;
  --color-danger: rgb(249, 84, 84);
}

body {
  font-family: "Montserrat", sans-serif;
  display: grid;
  place-items: center;
  color: var(--color-white);
  background: #ccc;
  line-height: 1.5;
}
#root {
  width: 100%;
}
.btn {
  background: var(--color-bg-dark);
  border-radius: 0.8rem;
  padding: 0.8rem;
  font-size: 1.6rem;
  color: var(--color-white);
  box-shadow: 0 1rem 1rem rgba(0, 0, 0, 0.2);
  transition: all 300ms ease;
  display: flex;
}

.btn.lg {
  padding: 0.8rem 1.5rem;
  font-size: 1.2rem;
  box-shadow: 0 1rem 1.5rem rgba(0, 0, 0, 0.4);
}

.btn.danger {
  background: var(--color-danger);
}

.btn.primary {
  background: var(--color-primary);
}

.btn:hover {
  cursor: pointer;
  box-shadow: none;
}

#app {
  background: var(--color-bg-black);
  height: 48rem;
  width: 100%;
  padding: 2rem 1.6rem;
  overflow-y: scroll;
  position: relative;
}

::-webkit-scrollbar {
  display: none;
}

h2,
h4 {
  font-weight: 500;
}

h2 {
  font-size: 2rem;
}

/* NOTES PAGE */
.notes-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1.6rem 1.5rem;
  background: var(--color-bg-black);
  z-index: 9;
}

.notes-header input {
  padding: 0.7rem 1rem;
  background: transparent;
  border: 1px solid var(--color-bg-dark);
  border-radius: 0.6rem;
  color: var(--color-white);
  font-size: 1.1rem;
  width: 100%;
  margin-right: 1.5rem;
}

.notes-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.2rem;
}
.notes-container > p {
  margin-top: 15px;
  text-align: center;
  font-size: 1.2rem;
  grid-column: 1/3;
}

.empty-notes {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.note {
  background: var(--color-primary);
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
  cursor: pointer;
  transition: all 300ms ease;
  color: var(--color-white);
  border-radius: 1rem;
}

.note h4 + p {

}

.note:hover {
  opacity: 0.9;
}

.note:nth-child(3) {
  grid-column: 1/3;
}

.note:nth-child(4) {
  grid-row: 3/5;
}

.note:nth-child(7) {
  grid-column: 1/3;
}

.note p:last-of-type {
  font-size: 0.8rem;
  opacity: 0.85;
}

.btn.add-btn {
  position: fixed;
  bottom: 4rem;
  right: 7rem;
  padding: 1rem;
}

/* CREATE NOTE & EDIT NOTE PAGE */
.create-note-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.create-note-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-top: 2rem;
}

.create-note-form input,
.create-note-form textarea {
  width: 100%;
  padding: 0.5rem 1rem;
  background: transparent;
  border-radius: 0.2rem;
  font-size: 1.1rem;
  color: var(--color-white);
}

.create-note-form input {
  font-size: 2rem;
}

/* MEDIA QUERIES FOR PHONES */
@media screen and (max-width: 768px) {
  #app {
    width: 100vw;
    height: 100vh;
    padding: 2rem 1rem;
  }

  .notes-header {
    width: 100%;
    padding: 2rem 1rem;
    position: fixed;
    top: 0;
    left: 0;
  }

  .notes-container {
    margin-top: 5rem;
    gap: 1rem;
  }

  .btn.add-btn {
    padding: 1.5rem;
    font-size: 1.5rem;
    bottom: 6%;
    right: 2rem;
  }
}
